import React, { useState } from 'react';
import {
  Box,
  Container,
  Typography,
  IconButton,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Stack,
  Alert,
} from '@mui/material';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { useNavigate } from 'react-router-dom';

interface LicenseGroup {
  title: string;
  licenseType: string;
  mainPackages: string[];
  otherPackages: string[];
  licenseText: string;
}

// Diese Array wird automatisch vom update-licenses.ps1 Skript generiert
// Run: .\update-licenses.ps1 im CouponVault Directory
const licenseGroups: LicenseGroup[] = [
  {
    title: 'MIT License',
    licenseType: 'MIT',
    mainPackages: ['react@19.1.0', 'react-native@0.81.5', 'expo~54.0.29', 'react-native-paper@^5.14.5'],
    otherPackages: ['placeholder - wird automatisch gefllt'],
    licenseText: 'MIT License - siehe update-licenses.ps1 fr vollstndige Lizenztexte',
  },
];

const LizenzenCompact: React.FC = () => {
  const navigate = useNavigate();
  const [expandedAccordion, setExpandedAccordion] = useState<string | false>(false);

  const handleAccordionChange = (panel: string) => (_event: React.SyntheticEvent, isExpanded: boolean) => {
    setExpandedAccordion(isExpanded ? panel : false);
  };

  return (
    <Box
      sx={{
        minHeight: '100vh',
        background: 'linear-gradient(180deg, #1C1B1F 0%, #2B2930 100%)',
        py: 4,
      }}
    >
      <Container maxWidth="md">
        <IconButton
          onClick={() => navigate('/')}
          sx={{
            color: '#D0BCFF',
            mb: 2,
            '&:hover': {
              background: 'rgba(208, 188, 255, 0.1)',
            },
          }}
        >
          <ArrowBackIcon />
        </IconButton>

        <Typography variant="h3" sx={{ color: 'white', fontWeight: 'bold', mb: 2 }}>
          Open Source Lizenzen / Licenses
        </Typography>

        <Alert severity="info" sx={{ mb: 4, bgcolor: 'rgba(76, 175, 80, 0.1)', borderColor: '#4CB050' }}>
          <Typography sx={{ fontSize: '0.95em' }}>
            CouponVault nutzt {licenseGroups.reduce((sum, g) => sum + g.mainPackages.length + g.otherPackages.length, 0)} Production-Dependencies in {licenseGroups.length} Lizenzgruppen.
          </Typography>
        </Alert>

        <Box sx={{ mb: 4 }}>
          <Typography variant="h5" sx={{ color: '#D0BCFF', fontWeight: 600, mb: 3 }}>
            Gruppierte Lizenzen
          </Typography>

          {licenseGroups.map((group) => (
            <Accordion
              key={group.licenseType}
              expanded={expandedAccordion === `group-${group.licenseType}`}
              onChange={handleAccordionChange(`group-${group.licenseType}`)}
              sx={{
                mb: 2,
                bgcolor: 'rgba(208, 188, 255, 0.05)',
                borderLeft: '4px solid #96CEB4',
                '&.Mui-expanded': {
                  m: '16px 0',
                },
                '&:before': {
                  display: 'none',
                },
              }}
            >
              <AccordionSummary expandIcon={<ExpandMoreIcon sx={{ color: '#D0BCFF' }} />}>
                <Typography variant="h6" sx={{ color: 'white', fontWeight: 600 }}>
                  {group.title}
                </Typography>
              </AccordionSummary>

              <AccordionDetails sx={{ bgcolor: 'rgba(0, 0, 0, 0.3)', pt: 3 }}>
                {group.mainPackages.length > 0 && (
                  <Box sx={{ mb: 3 }}>
                    <Typography variant="subtitle2" sx={{ color: '#FFB74D', fontWeight: 600, mb: 2 }}>
                       Haupt-Dependencies ({group.mainPackages.length})
                    </Typography>
                    <Stack spacing={1}>
                      {group.mainPackages.map((pkg) => (
                        <Typography key={pkg} sx={{ color: 'rgba(255, 255, 255, 0.9)', ml: 2 }}>
                           {pkg}
                        </Typography>
                      ))}
                    </Stack>
                  </Box>
                )}

                {group.otherPackages.length > 0 && (
                  <Box sx={{ mb: 3 }}>
                    <Typography variant="subtitle2" sx={{ color: '#B3E5FC', fontWeight: 600, mb: 2 }}>
                       Weitere Packages ({group.otherPackages.length})
                    </Typography>
                    <Stack spacing={0.5}>
                      {group.otherPackages.map((pkg) => (
                        <Typography key={pkg} sx={{ color: 'rgba(255, 255, 255, 0.7)', ml: 2, fontSize: '0.9em' }}>
                           {pkg}
                        </Typography>
                      ))}
                    </Stack>
                  </Box>
                )}

                <Box sx={{ mb: 2 }}>
                  <Typography variant="subtitle2" sx={{ color: '#B3E5FC', fontWeight: 600, mb: 2 }}>
                     Lizenztext:
                  </Typography>
                  <Box
                    sx={{
                      bgcolor: 'rgba(0, 0, 0, 0.5)',
                      p: 3,
                      borderRadius: 1,
                      maxHeight: '400px',
                      overflow: 'auto',
                    }}
                  >
                    <Typography
                      sx={{
                        color: 'rgba(255, 255, 255, 0.8)',
                        fontFamily: 'monospace',
                        fontSize: '0.85em',
                        lineHeight: 1.6,
                        whiteSpace: 'pre-wrap',
                        wordBreak: 'break-word',
                      }}
                    >
                      {group.licenseText}
                    </Typography>
                  </Box>
                </Box>
              </AccordionDetails>
            </Accordion>
          ))}
        </Box>

        <Box sx={{ mt: 6, p: 3, bgcolor: 'rgba(208, 188, 255, 0.1)', borderRadius: 2 }}>
          <Typography variant="h5" sx={{ color: 'white', mb: 2 }}>
            Zusammenfassung
          </Typography>
          <Typography sx={{ color: 'rgba(255, 255, 255, 0.8)', mb: 2 }}>
            Diese Website zeigt die Lizenzen aller Production-Dependencies von CouponVault.
            Hauptpakete sind mit  gekennzeichnet. Alle Lizenzen sind kommerziell nutzbar und Open-Source-konform.
          </Typography>
        </Box>
      </Container>
    </Box>
  );
};

export default LizenzenCompact;
